import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Cluster from '../views/Cluster.vue'
import AboutData from '../views/AboutData.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/',
    name: 'Cluster',
    component: Cluster
  },
  {
    path: '/aboutdata',
    name: 'AboutData',
    component: AboutData
  },
]

const router = new VueRouter({
  mode: "history",
  routes
})

export default router
