<template>
  <div>
      <v-card
      class="mx-auto"
      style="box-shadow: none;"
    >

      <v-img
        src="../assets/neuralwallpaper2.jpg"
        height="2.5em"
      ></v-img>
      <v-card-title class="justify-center">
        UMAP Visualization
      </v-card-title>
    </v-card>



    <v-container>
      <v-row
        class="mb-6"
        no-gutters
      >        
        <v-col
          lg=4>
          <v-select
            class="attr-select"
            dense
            filled
            rounded
            v-model="dataset"
            :items="datasets"
            placeholder="Select one"
            label="Select dataset"
          ></v-select>
        </v-col>
        <v-col
          lg=2>
          <v-btn
              class="property-button"
              elevation="2"
              @click="changeDatasetURL"
              color="primary"
            >Change dataset
          </v-btn>
        </v-col>
        <v-col
          lg=4>
          <v-select
            class="attr-select"
            dense
            filled
            rounded
            v-model="selectedColorProp"
            :items="songProps"
            placeholder="Select one"
            label="Property for the color"
          ></v-select>
        </v-col>
        <v-col
          lg=2>
          <v-btn
              class="controls property-button"
              elevation="2"
              color="primary"
            >Change color
          </v-btn>
        </v-col>
      </v-row>
    </v-container>

    <v-container fluid class="song-chart-container" justify="center" align="center">
      <v-row no-gutters
        justify="center"
        >
        <v-col
          lg=9
          md=9
          sm=12
          xs=12>

          <div id="chart-container">
            <div id="chart" ref="clusterchart"></div>
            <div id="loading"><span>loading...</span></div>
          </div>
        </v-col>
        <v-col
          lg=3
          md=3
          sm=12
          xs=12>

          <v-card v-if="this.$vuetify.breakpoint.lg || this.$vuetify.breakpoint.xl" class="selected-song-card-cluster">
            <v-card-title v-if="!$store.state.selectedSong" class="justify-center" >
              Ninguna cancion seleccionada
            </v-card-title>

            <v-card-title v-if="$store.state.selectedSong" class="justify-center" >
              {{$store.state.selectedSong.name}}
            </v-card-title>
            <v-card-subtitle v-if="$store.state.selectedSong" class="text-center">
              Album: {{$store.state.selectedSong.album}}
            </v-card-subtitle>
            <v-card-text v-if="$store.state.selectedSong"> 
              Year: {{$store.state.selectedSong.year}} <br>
            </v-card-text>
              <iframe :src="$store.state.selectedSongLink"
                width="100%" 
                style="max-height: 200!important"
                height="300"
                frameborder="0" 
                allowtransparency="true" 
                allow="encrypted-media">
                </iframe>  
          </v-card>

        </v-col>
      </v-row>
    </v-container>
    <!-- <v-btn
      elevation="2"
      @click="generateChart()"
    >show Song</v-btn>  -->

    <div>
      <!-- <Embed :source=source ref="embed" class="spotify-embed"> -->
      <Embed 
        v-if="$store.state.selectedSongLink && !this.$vuetify.breakpoint.lg && !this.$vuetify.breakpoint.xl"
        ref="embed" 
        class="spotify-embed">
      </Embed>
    </div>

  </div>


</template>

<script>
import { UMAP } from 'umap-js';
//import * as d3 from 'd3'
//import * as d3fc from 'd3fc'
import Embed from "../components/Embed.vue"

export default {
  name: 'Cluster',
  components: {
    Embed
  },
  created() {
    this.loadLibraries()
  },
  mounted() {
    // if(this.librariesLoaded)
    //   this.loadLibraries()
  },
  beforeDestroy() {
    this.$store.dispatch("clearSelectedSong")
  },
  data: () => {
    return {
      librariesLoaded: false,
      drawer: false,
      data: [],
      quadtree: null,
      svg: null,
      closestSong: null,
      selectedColorProp: "energy",
      dataset: "umapsongs_reduced.tsv",
      local: false,
      datasets: [
        "umapsongs.tsv",
        "umapsongs_reduced.tsv",
        "umapdata-n15-d0.1_ext.tsv",
        "umapdata-n50-d0.1_ext.tsv",
        "umapdata-n5-d0.1_ext.tsv",
        "umapdata-n15-d0.25_ext.tsv",
        "umapdata-n15-d0.5_ext.tsv",
        "umapdata-n15-d0_ext.tsv",
        ],


      songProps: [
        "acousticness",
        "danceability",
        "energy",
        "speechiness",
        "instrumentalness",
        "liveness",
        "valence",
        "tempo",
        "key",
        "loudness",
        "year"
        ],
      songPropsColor: [],
      }

  },
  methods: {
    loadLibraries() {
      const context = this
      let scripts = [
        //"https://unpkg.com/babel-polyfill@6.26.0/dist/polyfill.js",
        //"https://unpkg.com/custom-event-polyfill@0.3.0/custom-event-polyfill.js",
        "https://cdnjs.cloudflare.com/ajax/libs/document-register-element/1.8.0/document-register-element.js",
        "https://unpkg.com/d3@5.15.0/dist/d3.min.js",
        "https://unpkg.com/d3fc@14.2.3/build/d3fc.js",
        "https://unpkg.com/d3-svg-annotation@2.5.1/indexRollup.js"
      ];
      scripts.forEach(async script => {
        let tag = document.createElement("script");
        await tag.setAttribute("src", script);
        await document.head.appendChild(tag);
        console.log("imported", script)
        
        if (script == "https://unpkg.com/d3-svg-annotation@2.5.1/indexRollup.js"){
          console.log("All imported")
          this.librariesLoaded = true;
          setTimeout(function(){  context.generateChart() }, 3000);
          }
      });
    },
      clearChart(){
        // window.location.reload()
        // const div = this.$refs.clusterchart;
        // console.log(div.children)
        // console.log(div.children.foreach(child => {div.removeChild(child)}))
        //[].slice.call(div.children).forEach(function() {div.removeChild(child)})

      },
      changeDatasetURL() {
        this.$router.push({ path: '', query: { dataset: this.dataset }})
        window.location.reload()
      },
      generateChart() {
        try{
        const context = this
        const redraw = async () => {
          d3.select("#chart").datum({ annotations, data }).call(chart);
          
        };

        const seriesSvgAnnotation = () => {
          // the underlying component that we are wrapping
          const d3Annotation = d3.annotation();

          let xScale = d3.scaleLinear();
          let yScale = d3.scaleLinear();

          const join = fc.dataJoin("g", "annotation");

          const series = selection => {
            selection.each((data, index, group) => {
              const projectedData = data.map(d => ({
                ...d,
                x: xScale(d.x),
                y: yScale(d.y)
              }));

              d3Annotation.annotations(projectedData);

              join(d3.select(group[index]), projectedData).call(d3Annotation);
            });
          };

          series.xScale = (...args) => {
            if (!args.length) {
              return xScale;
            }
            xScale = args[0];
            return series;
          };

          series.yScale = (...args) => {
            if (!args.length) {
              return yScale;
            }
            yScale = args[0];
            return series;
          };

          fc.rebindAll(series, d3Annotation);

          return series;
        };

      // SERIES:JS
      // UTILS:JS
        const distance = (x1, y1, x2, y2) => {
          const dx = x1 - x2,
            dy = y1 - y2;
          return Math.sqrt(dx * dx + dy * dy);
        };

        const trunc = (str, len) =>
          str.length > len ? str.substr(0, len - 1) + "..." : str;

        const hashCode = s =>
          s.split("").reduce((a, b) => {
            a = (a << 5) - a + b.charCodeAt(0);
            return a & a;
          }, 0);

        const webglColor = color => {
          const { r, g, b, opacity } = d3.color(color).rgb();
          return [r / 255, g / 255, b / 255, opacity];
        };

        const iterateElements = (selector, fn) =>
          [].forEach.call(document.querySelectorAll(selector), fn);
        // UTILS:JS


        let data = [];
        let quadtree;

        const createAnnotationData = datapoint => ({
          note: {
            label: datapoint.album + " " + datapoint.year,
            bgPadding: 5,
            title: trunc(datapoint.name, 100)
          },
          x: datapoint.x,
          y: datapoint.y,
          dx: 20,
          dy: 20
        });

        // create a web worker that streams the chart data
        const streamingLoaderWorker = new Worker("./streaming-tsv-parser.js");
        streamingLoaderWorker.onmessage = ({
          data: { items, totalBytes, finished }
        }) => {
          const rows = items
            .map(d => ({
              ...d,
              x: Number(d.x),
              y: Number(d.y),
              acousticness:  Number(d.acousticness),
              danceability:  Number(d.danceability),
              energy:  Number(d.energy),
              year: Number(d.year),
              language: Number(d.year),

            }))
            .filter(d => d.year);
          data = data.concat(rows);

          if (finished) {
            document.getElementById("loading").style.display = "none";
            // COLOR__
            // compute the fill color for each datapoint
            context.songProps.forEach(property => {
              let scaler=1;
              let umbral = 0;

              if(property=="tempo")
                scaler = 1/160
              if(property=="key")
                scaler = 1/10
              if(property=="loudness")
                scaler = -1/20
              if(property=="year") {
                scaler = 1/40
                umbral = 1980;
              }

              const colorFill = d =>
                webglColor(yearColorScale(  (1-((d[property]-umbral) *scaler)) *100%100 ));
                
              context.songPropsColor.push(colorFill)
            });

            const fillColor = fc.webglFillColor().value(context.songPropsColor[context.songProps.indexOf(context.selectedColorProp)]).data(data);
            pointSeries.decorate(program => fillColor(program));

            // wire up the fill color selector
            iterateElements(".controls", el => {
              el.addEventListener("click", () => {
                fillColor.value(context.songPropsColor[context.songProps.indexOf(context.selectedColorProp)]);

                redraw();
              });
            });

            // create a spatial index for rapidly finding the closest datapoint
            quadtree = d3
              .quadtree()
              .x(d => d.x)
              .y(d => d.y)
              .addAll(data);
          }

          redraw();
        };

        if (context.$route.query.dataset != undefined)
          context.dataset = context.$route.query.dataset
        
        streamingLoaderWorker.postMessage(context.dataset);

        const languageColorScale = d3.scaleOrdinal(d3.schemeCategory10);
        const yearColorScale = d3
          .scaleSequential()
          .domain([0, 100])
          .interpolator(d3.interpolateRdYlGn);
        const xScale = d3.scaleLinear().domain([-5, 23]);
        const yScale = d3.scaleLinear().domain([-12, 20]);
        const xScaleOriginal = xScale.copy();
        const yScaleOriginal = yScale.copy();

        const pointSeries = fc
          .seriesWebglPoint()
          .equals((a, b) => a === b)
          .size(1)
          .crossValue(d => d.x)
          .mainValue(d => d.y);

        const zoom = d3
          .zoom()
          .scaleExtent([0.8, 10])
          .on("zoom", () => {
            // update the scales based on current zoom
            xScale.domain(d3.event.transform.rescaleX(xScaleOriginal).domain());
            yScale.domain(d3.event.transform.rescaleY(yScaleOriginal).domain());
            redraw();
          });

        const annotations = [];

        const pointer = fc.pointer().on("point", ([coord]) => {
          annotations.pop();

          if (!coord || !quadtree) {
            return;
          }

          // find the closes datapoint to the pointer
          const x = xScale.invert(coord.x);
          const y = yScale.invert(coord.y);
          const radius = Math.abs(xScale.invert(coord.x) - xScale.invert(coord.x - 20));
          context.closestSong = quadtree.find(x, y, radius);

          // if the closest point is within 20 pixels, show the annotation
          if (context.closestSong) {
            annotations[0] = createAnnotationData(context.closestSong);
          }

          redraw();
        });

        d3.select("#chart").on("click", function() {
            console.log(context.closestSong);
            context.$store.dispatch(
              "changeSong",
              context.closestSong
              )
        })


        // ANOTATIONS
        const annotationSeries = seriesSvgAnnotation()
          .notePadding(15)
          .type(d3.annotationCallout);

        const chart = fc
          .chartCartesian(xScale, yScale)
          .webglPlotArea(
            // only render the point series on the WebGL layer
            fc
              .seriesWebglMulti()
              .series([pointSeries])
              .mapping(d => d.data)
          )
          .svgPlotArea(
            // only render the annotations series on the SVG layer
            fc
              .seriesSvgMulti()
              .series([annotationSeries])
              .mapping(d => d.annotations)
          )
          .decorate(sel =>
            sel
              .enter()
              .select("d3fc-svg.plot-area")
              .on("measure.range", () => {
                xScaleOriginal.range([0, d3.event.detail.width]);
                yScaleOriginal.range([d3.event.detail.height, 0]);
              })
              .call(zoom)
              .call(pointer)
          );

        // render the chart with the required data
        // Enqueues a redraw to occur on the next animation frame
      } catch (error) {
        console.log(error)
        window.location.reload()
      }


      }
  }
}
</script>

