import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    selectedSongLink: undefined,
    selectedSong: undefined,
    selectedClusterDataSet: "umapsongs_reduced.tsv",
  },
  mutations: {
    setSelectedSongLink(state, val) {
      state.selectedSongLink = val;
    },
    setSelectedClusterDataSet(state, val) {
      state.ClusterDataSet = val;
    },
    setSelectedSong(state, val) {
      if (val["artists"] != undefined) {
        let artists = val["artists"]
        artists = artists.slice(1, artists.length-1)
        artists = artists.split(',')
        artists = artists[0].slice(1, artists[0].length-1)
        val["artists"] = artists
      }
      state.selectedSong = val;
    },
    clearSelectedSong(state){
      state.selectedSongLink = "";
      state.selectedSong = undefined;
    },
  },
  actions: {
    changeSong({commit}, song) {
      let songLink = changeLink(song.id);
      commit('setSelectedSongLink', songLink);
      commit('setSelectedSong', song);
    },
    changeSelectedClusterDataSet({commit}, dataset) {
      commit('setSelectedClusterDataSet', dataset);
    },
    clearSelectedSong({commit}) {
      commit('clearSelectedSong');
    },
  },
  modules: {
  }
})

function changeLink(id, type="track") {
  let uri = "https://open.spotify.com/embed/";
  let link = uri + type + "/" + id;
  return link;
}