<template>
  <div>
    <div id="songsChart"></div>
  </div>
</template>

<script>

import * as d3 from 'd3'
import * as d3Collection from 'd3-collection';

export default {
  name: "Chart",
  props: ["songsFilters"],
  data:() => ({
      svg: null,
      x: null,
      y: null,
      dotRadius: 3,
      dotRadiusSelected: 5,
      data: null,
      dotsColor: "#69b3a2",
      x_prop: null, 
      y_prop: null,
      color_prop: null,
  }),
  mounted() {
    this.colourScale = d3.scaleOrdinal().range(["#5EAFC6","#FE9922","#93c464","#75739F"]);
    this.songsChart("acousticness", "danceability", null)
    // this.songsChart()
  },
  methods: {
    async songsChart(x_prop, y_prop, color_prop) {
      this.x_prop = x_prop,
      this.y_prop = y_prop
      this.color_prop = color_prop
      let context = this;
      console.log("Generating chart")

      var margin = {top: 10, right: 30, bottom: 30, left: 60},
          width = 460 - margin.left - margin.right,
          height = 500 - margin.top - margin.bottom;

      this.clearAll()
      width = parseInt(d3.select("#songsChart").style('width'), 10)  - margin.left - margin.right
      // append the svg object to the body of the page
      // Just if not previously created
      this.svg = d3.select("#songsChart")
        .append("svg")
          .attr("width", width + margin.left + margin.right)
          .attr("height", height + margin.top + margin.bottom)
        .append("g")
          .attr("transform",
                "translate(" + margin.left + "," + margin.top + ")");

      //Read the data
      if(this.data == null)
        this.data = await d3.csv("./songs_reduced.csv");
      

      let xmax = this.getMaxValue(this.data, x_prop)
      let ymax = this.getMaxValue(this.data, y_prop)


      // Add X axis
      this.x = d3.scaleLinear()
        .domain([0, xmax])
        .range([ 0, width ]);
      this.svg.append("g")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(this.x));
      this.svg.append("text")
        .style("text-anchor", "end")
          .attr("x", width)
          .attr("y", height - 8)
        .text(x_prop);

      // Add Y axis
      this.y = d3.scaleLinear()
        .domain([0, ymax])
        .range([ height, 0]);
      this.svg.append("g")
        .call(d3.axisLeft(this.y));
      this.svg.append("text")
        .attr("transform", "rotate(-90)")
        .attr("y", 6)
        .attr("dy", "1em")
        .style("text-anchor", "end")
        .text(y_prop);
        

      // Add dots
      this.addDots(this.data, x_prop, y_prop, color_prop)

      this.addClickEvent();


    },

    invalidSong(song) {
      let multiplier = 100
      let invalid = false;
      this.songsFilters.forEach(filter => {
        
        if (filter[0]=="popularity")
          multiplier = 1
        else
          multiplier = 100

        if (song[filter[0]]*multiplier < filter[1][0] || 
            song[filter[0]]*multiplier > filter[1][1] ){
          invalid = true;
        }
      });
      return invalid

    },

    addDots(data, x_prop, y_prop, color_prop) {
      let context = this

      var myColor = d3.scaleLinear().domain([context.getMinValue(this.data, color_prop),context.getMaxValue(this.data, color_prop)])
        .range(["white", "#005B47"])

      this.svg.append('g')
        .selectAll("dot")
        .data(data)
        .enter()
        .append("circle")
          .attr("cx", function (d) { 
            if (context.invalidSong(d))
              return 999999999
            else
              return context.x(d[x_prop]); 
            } )
          .attr("cy", function (d) { return context.y(d[y_prop]); } )
          .attr("json", function (d) { return JSON.stringify(d); } )
          .attr("r", this.dotRadius)
          .style("fill", function(d){
            if (color_prop)
              return myColor(d[color_prop]) 
            else
              return context.dotsColor
            })
          // .style("fill", "#69b3a2")
          // .style("stroke","green")
    },

    addClickEvent() {
      let context = this;

      var myColor = d3.scaleLinear().domain([context.getMinValue(this.data, context.color_prop),context.getMaxValue(this.data, context.color_prop)])
        .range(["white", "#005B47"])

      this.svg.selectAll("circle").on("click", function(){
                  let selectedSongJSON = d3.select(this).attr("json")
                  selectedSongJSON = JSON.parse(selectedSongJSON)
                  context.$store.dispatch(
                    "changeSong",
                    selectedSongJSON
                    )

                  // Deseleccionamos los anteriores
                  d3.selectAll('circle')
                  .attr("r", context.dotRadius)
                  .style("fill", function(d){
                    if (context.color_prop)
                      return myColor(d[context.color_prop]) 
                    else
                      return context.dotsColor
                    })
                  .style("stroke","none")

                  // Seleccionamos el actual
                  d3.select(this).attr('r', context.dotRadiusSelected)
                      .style("fill","lightcoral")
                      .style("stroke","red");
              });
    },

    clearDots() {
       d3.selectAll('circle').remove();
    },

    clearText() {
       d3.selectAll('text').remove();
    },

    clearAll() {
      d3.selectAll("svg").remove();
    },

    getMaxValue(data, attr) {
      let maxvalue = 0;
      data.forEach(element => {
        if( maxvalue < element[attr] ) 
          maxvalue = element[attr]
      });

      if(maxvalue < 1)
        maxvalue = 1
      
      return maxvalue
    },

    getMinValue(data, attr) {
      let minvalue = 999999;
      data.forEach(element => {
        if( minvalue > element[attr] ) 
          minvalue = element[attr]
      });

      if(minvalue <= 0.1)
        minvalue = -0.2

      return minvalue
    }
  },
  computed: {

  }
}

</script>
